  
  /* div#layout {
    text-align: center;
  }
   */
  div#container {
    width: 1000px;
    height: 600px;
    margin: auto;
    background-color: #3F51B5;
  }
  
  svg {
    width: 100%;
    height: 100%;
  }
  
  .bar {
    fill: #92B4F4;
  }
  
  text {
    font-size: 12px;
    fill: #E1EFF6;
  }
  
  #container path {
    stroke: #E1EFF6;
  }
  
  line {
    stroke: #E1EFF6;
  }
  
  line#limit {
    stroke: #FED966;
    stroke-width: 3;
    stroke-dasharray: 3 6;
  }
  
  .grid path {
    stroke-width: 0;
  }
  
  .grid .tick line {
    stroke: #9FAAAE;
    stroke-opacity: 0.3;
  }
  
  text.divergence {
    font-size: 14px;
    fill: #2F4A6D;
  }
  
  text.value {
    font-size: 14px;
  }
  
  text.title {
    font-size: 22px;
    font-weight: 600;
  }
  
  text.label {
    font-size: 14px;
    font-weight: 400;
  }
  
  text.source {
    font-size: 10px;
  }